.App-Intro {
    font-size: 5vh;
    color: white;
}

.Intro1 {
    background: linear-gradient(to right, #8722a9, #288ea2); /* Standard syntax */
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text; /* Standard syntax */
    color: #8722a9;      /* Fallback text color if gradient clip fails */ /* same color as the first color of the gradient */
}

.App-landingText {
    margin-top: 200px;
    padding: 0 200px;
    float: left;
    display: flex;
    flex-direction: column;
    text-align: center;
    .App-Logo {
        margin-bottom: 10px;
    }
}

.App-bio {
    color: #727c94;
}

.App-CVDown {
    background: linear-gradient(to right, #8722a9, #288ea2); /* Standard syntax */
    padding: 10px;
    border-radius: 25px;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    font-size: 18px;
}
a {
    margin-top: 20px;
    text-decoration: none;
    color: white;
}