// Variables
$primary-color: #8722a9;
$secondary-color: #288ea2;
$background-color: #0f1724;
$logo-size: 400px;

.App-Logo {
    position: absolute;
    top: 15%;
    right: 0;
    width: $logo-size;
    height: $logo-size;
    margin-bottom: 20px; /* Space below the logo */
    border-radius: 50%; // Make logo round
    float: right;
    margin-right: 300px;
    background-color: $background-color;
}