.App {
  text-align: center;
}

.App-Header {
  display: flex;
  justify-content: space-between; /* Distribute items along main axis */
  align-items: center;           /* Center vertically */
  padding: 0 200px;                /* Add some padding to prevent content from touching the edges */
    background-color: #0F1724;
    width: 100%;
    height: 100px;
    position: sticky;
    top: 0;
    z-index: 1;
}

.App-Title {
  color: white;
  margin-right: 100px;
  font-weight: 700;
}

.App-Navigation {
  display: flex; /* Ensure navigation items are inline */
  justify-content: center;  /* Not strictly necessary if space-between is used on App-Header */
  /* Any additional navigation styling */
  gap: 20px;
}

.App-Navigation .App-Header-NavLink { /* Space between nav links */
  margin: 0 30px;
}


.App-Navigation .App-Header-NavLink a {
  text-decoration: none; /*Remove underline from the links*/
  color: white;
}

.fa-icons {
  font-size: 24px;
  margin: 15px;
}